@import "mapbox-gl/dist/mapbox-gl.css";
@import "mapbox-gl-controls/theme.css";
@import "@mapbox/mapbox-gl-geocoder/dist/mapbox-gl-geocoder.css";

html,
body,
#map {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
}

.mapboxgl-popup {
  width: auto;
  font: 12px/20px "Helvetica Neue", Arial, Helvetica, sans-serif;
}

.btn {
  border: none;
  border-radius: 2px;
  background-color: #17a2b8;
  color: #ffffff;
  margin: 10px 0 0 0;
  padding: 7px 0;
  font-size: 14px;
  font-weight: bold;
  cursor: pointer;
  width: 100%;
}

.btn:hover {
  background: #0e6370;
}

